@forward 'global';
@import 'vars';

#grid-container{
  display: grid;
  grid-template-columns: repeat(2, 265px);
  grid-template-rows: minmax(40px, auto) repeat(2, 220px);
  //god. why didn't I use template rows and columns? I'm so stupid lmao.
  grid-template-areas:
  "welcome-msg welcome-msg"
  "about-me-elem journal-elem"
  "portfolio-elem portfolio-elem";
  gap: 10px;

  >.grid-elem:first-child{
    grid-area: welcome-msg;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: larger;
    user-select: none;
    overflow-wrap: anywhere;
  }
  
  >.grid-elem:last-child{
    grid-area: portfolio-elem;
    >.grid-desc{
      left: ($pos-x-y - 2%);
    }
  }
}

.grid-elem{
  display: flex;
  position: relative;
  
  background-color: rgba($color: #000000, $alpha: 0.3);
  border: solid black;
  border-radius: 5px;
  box-shadow: black 3px 3px 6px 1px;
  
  transition: 0.15s;
  
  >img{
    margin: auto;
    width: 125px;
  }
}

.grid-elem:hover{
  box-shadow: black 5px 5px 10px 3px;
  transform: translate(-2px, -2px);
}

.grid-elem:active{
  box-shadow: black 1px 1px 1px 0px;
  transform: translate(3px, 3px);
}

.grid-desc{
  position: absolute;
  left: $pos-x-y;
  bottom: $pos-x-y;
  font-size: larger;
}

// (320px <= width <= 450px) and (677px <= height <= 932px) pls firefox support this no one likes to write this ugly min-width max width thing
// ((min-width: 320px) and (max-width: 450px))
// ((min-width: 320px) and (max-width: 768px))

//  Mobile portrait mode
@media screen and (min-width: 320px) and (max-width: 480px) and (orientation: portrait) {
  #grid-container{
    grid-template-columns: repeat(2, 45vw);
    grid-template-rows: minmax(5dvh, auto) repeat(2, 30dvh);
  }

  .grid-elem{
    >img{
      width: 28vw;
    }
  }
}
//((320px <= height <= 450px) and (677px <= width <= 932px))
//  Mobile landscape mode
@media screen and (min-width:768px) and (max-width:950px) and (orientation: landscape) {
  #grid-container{
    grid-template-columns: repeat(2, 25vw);
    grid-template-rows: minmax(6dvh, auto) repeat(2, 38dvh);
  }

  .grid-elem{
    >img{
      width: 15vw;
    }
  }
}